import React from 'react';
import logo from './react.svg';
import axios from 'axios';
import { Input, Layout, Row, Col, Button  } from 'antd';
import autobind from 'autobind-decorator'

const { TextArea  } = Input;
const { Content } = Layout;

import './Home.css';

class Home extends React.Component {
	state = {
		source: '',
		change: ''
	}
	@autobind
	changeSource(e) {
		this.setState({
			source: e.target.value
		})
	}
	@autobind
	changeVal(e) {
		this.setState({
			change: e.target.value
		})
	}
	@autobind
	createSample() {
		axios.post('/run', ...this.state).then(res => console.log(res))
	}
  render() {
	  const { source, change } = this.state;
    return (
      <div className="Home">
			<Layout style={{ padding: '15px'  }}>
			  <Content>
					<Row>
						  <Col span={12}>
							<TextArea autosize={{ minRows: 8, maxRows: 50 }} value={source} onChange={this.changeSource}/>
						</Col>
					  <Col span={12}>
						<TextArea autosize={{minRows: 8, maxRows: 20}} onChange={this.changeVal} value={change}/>
						<Button type="primary" onClick={this.createSample}>生成样例</Button>
						<Button type="danger">开始创建</Button>
					</Col>
					</Row>

			  </Content>
			</Layout>
      </div>
    );
  }
}

export default Home;
